import { debug } from '@/utils/utils';
import { Element, Event, EventEmitter, Watch } from '@stencil/core';
import { Component, h, Listen, Prop, State } from '@stencil/core';

@Component({
  tag: 'time-period-selector',
  styleUrl: 'time-period-selector.scss',
  scoped: true,
})
export class TimePeriodSelector {
  @Element() host!: HTMLElement;

  // The possible values for the time periods dropdown
  @Prop() timePeriods: string[] = ['hour', 'day', 'week', 'month'];

  /**
   * The default selected time period.
   */
  @Prop() defaultSelectedPeriod: string = 'hour';

  /**
   * The default selected number.
   */
  @Prop() defaultSelectedNumber: number = 1;

  /**
   * The currently selected time period
   */
  @State() selectedPeriod: string = this.defaultSelectedPeriod;

  /**
   * The currently selected number of the time period
   */
  @State() selectedNumber: number = this.defaultSelectedNumber;

  /*
   * The options for the number dropdown, to be calculated based on the selectedPeriod
   */
  @State() numberOptions: { label: string; value: string }[] = this.calculateOptions('hour').map(i => {
    return {
      label: i.toString(),
      value: i.toString(),
    };
  });

  /**
   * The options for the time period dropdown
   */
  @State() timePeriodOptions = this.timePeriods.map(period => {
    return {
      label: period,
      value: period,
    };
  });

  @Watch('defaultSelectedPeriod')
  defaultSelectedPeriodChangedHandler(newValue: string) {
    this.selectedPeriod = newValue;
    this.updateNumberOptionsAndSelectedNumber(newValue);
  }

  @Watch('defaultSelectedNumber')
  defaultSelectedNumberChangedHandler(newValue: number) {
    this.selectedNumber = newValue;
  }

  @Watch('timePeriods')
  timePeriodsChangedHandler(newValue: string[]) {
    this.timePeriodOptions = newValue.map(period => {
      return {
        label: period,
        value: period,
      };
    });
  }

  @Event() timePeriodChanged!: EventEmitter<{ number: number; period: string }>;

  private calculateOptions(period: string): number[] {
    switch (period) {
      case 'hour':
        return Array.from({ length: 23 }, (_, i) => i + 1);
      case 'minute':
        return Array.from({ length: 60 }, (_, i) => i);
      case 'day':
        return Array.from({ length: 30 }, (_, i) => i + 1);
      case 'week':
        return Array.from({ length: 4 }, (_, i) => i + 1);
      case 'month':
        return Array.from({ length: 12 }, (_, i) => i + 1);
      default:
        return [];
    }
  }

  @Listen('selectedOptionChanged')
  selectedOptionChangedHandler(event: CustomEvent<{ value: string; name: string }>) {
    debug('time-period-selector', 'selectedOptionChangedHandler', event.detail);
    const { value, name } = event.detail;
    if (name === 'time-period') {
      this.selectedPeriod = value;
      this.updateNumberOptionsAndSelectedNumber(value);
    } else if (name === 'time-number') {
      this.selectedNumber = parseInt(value);
    }
    const selected = {
      number: this.selectedNumber,
      period: this.selectedPeriod,
    };
    this.timePeriodChanged.emit(selected);
  }

  updateNumberOptionsAndSelectedNumber(period: string) {
    const numberOptions = this.calculateOptions(period);
    this.numberOptions = numberOptions.map(i => {
      return {
        label: i.toString(),
        value: i.toString(),
      };
    });
    this.selectedNumber = parseInt(this.numberOptions[0].value);
  }

  render() {
    return (
      <div class="time-period-selector">
        <select-dropdown
          id="time-number"
          name={'time-number'}
          exportparts="sd__dropdown: tps__number-dropdown, sd__dropdown-button: tps__number-dropdown-button, sd__dropdown-content: tps__number-dropdown-content"
          options={this.numberOptions}
          defaultSelectedOption={this.numberOptions.find(i => i.value == this.selectedNumber.toString()) ?? this.numberOptions[0]}
          withSearch={false}
        />
        <select-dropdown
          id="time-period"
          name={'time-period'}
          options={this.timePeriodOptions}
          exportparts="sd__dropdown: tps__period-dropdown, sd__dropdown-button: tps__period-dropdown-button, sd__dropdown-content: tps__period-dropdown-content"
          pluralizedLabel={this.selectedNumber > 1 ? 's' : ''}
          defaultSelectedOption={this.timePeriodOptions.find(i => i.value == this.selectedPeriod) ?? this.timePeriodOptions[0]}
          withSearch={false}
        />
      </div>
    );
  }
}
